import React, { useEffect, useState } from "react";
import * as XLSX from 'xlsx';

function Dashboard(){
    const [items, setItems] = useState([]);

    const readExcel = (file) => {
        const promise = new Promise((resolve, reject) => {
            const fileReader = new FileReader();
            fileReader.readAsArrayBuffer(file)

            fileReader.onload = (e) => {
                const bufferArray = e.target.result;
                
                const wb = XLSX.read(bufferArray, {type:'buffer'});
                const wsname = wb.SheetNames[0];

                const ws = wb.Sheets[wsname];
                const data = XLSX.utils.sheet_to_json(ws);

                resolve(data);
            };

            fileReader.onerror = (error) => {
                reject(error);
            };
        });

        promise.then((d) => {
            console.log(d);
            setItems(d);
        });
    }

    return(
        <div className="container micontenedor">
                <h1>Dashboard</h1>
                <div class="container">
                    <div class="row justify-content-around">
                        <div class="col">
                            <div class="row align-items-center">
                                <h1 class="text-center"> Registro de Archivos</h1>
                            </div>
                            <div class="row justify-content-center">
                                <div class="col-md-auto">
                                    <input type="file" class="form-control" onChange={(e)=>{
                                        const file = e.target.files[0];
                                        readExcel(file);
                                    }}/>
                                </div>
                            </div>
                        </div>
                        <div class="col">
                            <div class="row justify-content-center">
                                <div class="col-md-auto">
                                    <a href="#nyModal" class="btn btn-dark btn-lg" data-bs-toggle="modal" role="button">Generar Reporte 1</a>
                                </div>
                                <div id="myModal" class="modal fade">
                                    <div class="modal-dialog">
                                        <div class="modal-content">
                                            <div class="modal-header">
                                                <h5 class="modal-title">Confirmation</h5>
                                                <button type="button" class="btn-close" data-bs-dismiss="modal"></button>
                                            </div>
                                            <div class="modal-body">
                                                <p>Do you want to save changes to this document before closing?</p>
                                                <p class="text-secondary"><small>If you don't save, your changes will be lost.</small></p>
                                            </div>
                                            <div class="modal-footer">
                                                <button type="button" class="btn btn-secondary" data-bs-dismiss="modal">Cancel</button>
                                                <button type="button" class="btn btn-primary">Save changes</button>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div class="row justify-content-center">
                                <div class="col-md-auto">
                                    <button class="btn btn-dark btn-lg">Generar Reporte 2</button>
                                </div>
                            </div>
                            <div class="row justify-content-center">
                                <div class="col-md-auto">
                                    <button class="btn btn-dark btn-lg">Generar Reporte 3</button>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>



                <table class="table">
                    <thead>
                        <tr>
                            <th scope="col">Item</th>
                            <th scope="col">Description</th>
                        </tr>
                    </thead>
                    <tbody>
                        { items.map((d) => (
                            <tr key={d.Item}>
                                <th>{d.Item}</th>
                                <td>{d.Description}</td>
                            </tr>
                        ))}
                    </tbody>
                </table>
        </div>
    );
}

export default Dashboard;